"use client"

import { useState, useCallback } from "react"
import { BrowserProvider, parseEther } from "ethers"
import { useWallet } from "@/hooks/use-wallet"
import toast from "react-hot-toast"

/**
 * Hook for x402 micropayments (middleware-based)
 */
export function useX402() {
  const { address, isConnected } = useWallet()
  const [loading, setLoading] = useState(false)

  const getProvider = useCallback(() => {
    if (typeof window === "undefined" || !window.ethereum) {
      throw new Error("MetaMask not installed")
    }
    return new BrowserProvider(window.ethereum as any)
  }, [])

  const generatePaymentProof = useCallback(async (amount: bigint, route: string) => {
    // Generate a payment proof
    // In production, this would create an on-chain payment or x402 stream
    const timestamp = Date.now()
    const nonce = `0x${Array.from({ length: 32 }, () =>
      Math.floor(Math.random() * 256).toString(16).padStart(2, "0")
    ).join("")}`
    
    // For testnet, we'll create a simple proof
    // In production, this would be an actual on-chain transaction hash or x402 stream ID
    return {
      proof: `${address}-${amount}-${timestamp}-${nonce}`,
      timestamp,
      nonce,
    }
  }, [address])

  /**
   * Pay to view model details
   */
  const payToViewDetails = useCallback(
    async (modelId: number): Promise<boolean> => {
      if (!isConnected || !address) {
        toast.error("Please connect your wallet")
        return false
      }

      setLoading(true)
      try {
        // Get payment amount from API
        const amountsRes = await fetch("/api/payment/amounts")
        const amounts = await amountsRes.json()
        const paymentAmount = BigInt(amounts.viewDetails.amountWei)

        // Generate payment proof
        const { proof } = await generatePaymentProof(paymentAmount, "GET /api/models/[id]/details")

        // Make payment request to API
        const res = await fetch(`/api/models/${modelId}/details`, {
          method: "GET",
          headers: {
            "x-payment-proof": proof,
            "x-payment-address": address,
            "x-payment-amount": paymentAmount.toString(),
          },
        })

        if (res.status === 402) {
          const data = await res.json()
          toast.error(data.error || "Payment required")
          return false
        }

        if (!res.ok) {
          throw new Error("Payment failed")
        }

        toast.success("Payment successful! Viewing details...")
        return true
      } catch (error: any) {
        console.error("View details payment error:", error)
        toast.error(error?.message || "Payment failed")
        return false
      } finally {
        setLoading(false)
      }
    },
    [address, isConnected, generatePaymentProof]
  )

  /**
   * Pay for competition entry
   */
  const payForCompetitionEntry = useCallback(
    async (
      competitionId: number,
      modelId: number,
      entryFee: bigint
    ): Promise<boolean> => {
      if (!isConnected || !address) {
        toast.error("Please connect your wallet")
        return false
      }

      setLoading(true)
      try {
        // Get payment amount from API
        const amountsRes = await fetch("/api/payment/amounts")
        const amounts = await amountsRes.json()
        const micropaymentAmount = BigInt(amounts.competitionEntry.amountWei)

        // Generate payment proof
        const { proof } = await generatePaymentProof(micropaymentAmount, "POST /api/competitions/[id]/enter")

        // Make payment request to API
        const res = await fetch(`/api/competitions/${competitionId}/enter`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            "x-payment-proof": proof,
            "x-payment-address": address,
            "x-payment-amount": micropaymentAmount.toString(),
          },
          body: JSON.stringify({
            modelId,
            entryFee: entryFee.toString(),
            paymentProof: proof,
            paymentAddress: address,
            paymentAmount: micropaymentAmount.toString(),
          }),
        })

        if (res.status === 402) {
          const data = await res.json()
          toast.error(data.error || "Payment required")
          return false
        }

        if (!res.ok) {
          throw new Error("Competition entry failed")
        }

        toast.success("Successfully entered competition!")
        return true
      } catch (error: any) {
        console.error("Competition entry payment error:", error)
        toast.error(error?.message || "Entry failed")
        return false
      } finally {
        setLoading(false)
      }
    },
    [address, isConnected, generatePaymentProof]
  )

  /**
   * Pay for deposit (investment)
   */
  const payForDeposit = useCallback(
    async (modelId: number, amount: bigint): Promise<boolean> => {
      if (!isConnected || !address) {
        toast.error("Please connect your wallet")
        return false
      }

      setLoading(true)
      try {
        // Get payment amount from API
        const amountsRes = await fetch("/api/payment/amounts")
        const amounts = await amountsRes.json()
        const depositFee = BigInt(amounts.deposit.amountWei)

        // Generate payment proof
        const { proof } = await generatePaymentProof(depositFee, "POST /api/models/[id]/invest")

        // Make payment request to API
        const res = await fetch(`/api/models/${modelId}/invest`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            "x-payment-proof": proof,
            "x-payment-address": address,
            "x-payment-amount": depositFee.toString(),
          },
          body: JSON.stringify({
            amount: amount.toString(),
            paymentProof: proof,
            paymentAddress: address,
            paymentAmount: depositFee.toString(),
          }),
        })

        if (res.status === 402) {
          const data = await res.json()
          toast.error(data.error || "Payment required")
          return false
        }

        if (!res.ok) {
          throw new Error("Deposit failed")
        }

        toast.success("Deposit successful!")
        return true
      } catch (error: any) {
        console.error("Deposit payment error:", error)
        toast.error(error?.message || "Deposit failed")
        return false
      } finally {
        setLoading(false)
      }
    },
    [address, isConnected, generatePaymentProof]
  )

  /**
   * Get payment amounts from API
   */
  const getPaymentAmounts = useCallback(async () => {
    try {
      const res = await fetch("/api/payment/amounts")
      if (!res.ok) {
        throw new Error("Failed to fetch payment amounts")
      }
      
      const amounts = await res.json()
      
      return {
        viewDetails: BigInt(amounts.viewDetails.amountWei),
        competitionEntry: BigInt(amounts.competitionEntry.amountWei),
        deposit: BigInt(amounts.deposit.amountWei),
      }
    } catch (error) {
      console.error("Error fetching payment amounts:", error)
      // Return defaults for testnet
      return {
        viewDetails: parseEther("0.0001"), // 0.0001 ETH
        competitionEntry: parseEther("0.0005"), // 0.0005 ETH
        deposit: parseEther("0.0002"), // 0.0002 ETH
      }
    }
  }, [])

  return {
    payToViewDetails,
    payForCompetitionEntry,
    payForDeposit,
    getPaymentAmounts,
    loading,
  }
}

